// ** React Imports
import { Fragment, useEffect, useState } from 'react'
// ** Table Columns
import { columns } from './columns'
import * as FileSaver from "file-saver"
import * as XLSX from "xlsx"
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, MoreVertical, Download } from 'react-feather'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Input,
    DropdownMenu,
    DropdownItem,
    DropdownToggle,
    UncontrolledDropdown,
    Button
} from 'reactstrap'

import Select from 'react-select'
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import moment from 'moment'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, exportList, applyFileter, dataId }) => {
    // ** Converts table to CSV
    const options = [
        { id: 1, name: 'All Time', startDate: '', endDate: '' },
        { id: 2, name: 'This Month', startDate: moment().startOf('M').valueOf(), endDate: moment().endOf('M').valueOf() },
        { id: 3, name: 'Last Month', startDate: moment().subtract(1, 'M').startOf('M').valueOf(), endDate: moment().subtract(1, 'M').endOf('M').valueOf() },
        { id: 6, name: 'This Year', startDate: moment().set('M', 3).startOf('M').valueOf(), endDate: moment().set('M', 2).add(1, 'Y').endOf('M').valueOf() },
        { id: 8, name: 'Custom Range', startDate: '', endDate: '' }
    ]

    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')
    const [selectOpt, setSelecOpt] = useState(1)

    const onDateChange = dates => {
        const [start, end] = dates
        setStartDate(start)
        setEndDate(end)

        if (start && end) {
            applyFileter({ id: selectOpt, startDate: moment(start).valueOf() || '', endDate: moment(end).valueOf() || "", flag: true })
        }
    }

    const resetFilter = () => {
        setStartDate('')
        setSelecOpt(1)
        setEndDate('')
        applyFileter({ id: 1, name: 'All Time', startDate: '', endDate: "", flag: true })
    }
    return (
        <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
            <Row>
                <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
                    <div className='d-flex align-items-center w-100'>
                        <label htmlFor='rows-per-page'>Show</label>
                        <Input
                            className='mx-50'
                            type='select'
                            id='rows-per-page'
                            value={rowsPerPage}
                            onChange={handlePerPage}
                            style={{ width: '5rem' }}
                        >
                            <option value='10'>10</option>
                            <option value='25'>25</option>
                            <option value='50'>50</option>
                        </Input>
                        <label htmlFor='rows-per-page'>Entries</label>

                        <Col className='me-1'>
                            <Select
                                className={'react-select ms-1 w-100'}
                                classNamePrefix='select'
                                options={options}
                                onChange={val => { applyFileter(val); setSelecOpt(val.id) }}
                                getOptionLabel={(option) => option.name}
                                getOptionValue={(option) => option.id}
                                value={options.find(oj => oj.id === selectOpt) || {}}
                            />
                        </Col>

                        {
                            (dataId === 8) &&
                            <Col className={'ms-1'}>
                                <Flatpickr
                                    onChange={(date) => { onDateChange(date) }}
                                    startdate={startDate}
                                    selected={startDate}
                                    enddate={endDate}
                                    options={{ mode: 'range', altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                                    className='form-control invoice-edit-input date-picker'
                                />
                            </Col>
                        }
                        <Button outline color='primary' size='sm' className='ms-1' onClick={() => { resetFilter() }}>Clear Filter</Button>
                    </div>
                </Col>
                <Col
                    xl='6'
                    className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
                >
                    <div className='d-flex align-items-center table-header-actions ms-1'>
                        <UncontrolledDropdown className='me-1'>
                            <DropdownToggle color='secondary' caret outline>
                                <MoreVertical className='font-small-4 me-50' />
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem className='w-100' onClick={() => { exportList() }}>
                                    <Download className='font-small-4 me-50' />
                                    <span className='align-middle'>Export</span>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </div>
                </Col>
            </Row>
        </div>
    )
}

const UsersList = () => {

    // ** States
    const [sort, setSort] = useState('desc')
    const [searchTerm, setSearchTerm] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [sortColumn, setSortColumn] = useState('createdon')
    const [data, setData] = useState([])
    const [total, setTotal] = useState([])
    const [rowsPerPage, setRowsPerPage] = useState(10)
    const [currentStatus] = useState({ value: '', label: 'Select Status', number: 0 })
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')
    const [dataId, setDataId] = useState(1)

    const getSummaries = (params) => {
        axios.post('/taskreport/performancereport', params)
            .then((res) => {
                setData(res.data.result.results)
                setTotal(res.data.result.total)
            })
            .catch((err) => {
                console.log(err)
            })
    }

    // ** Get data on mount
    useEffect(() => {
        getSummaries({
            sort,
            sortColumn,
            q: searchTerm,
            page: currentPage,
            perPage: rowsPerPage,
            status: currentStatus.value,
            start: startDate,
            end: endDate
        })

    }, [startDate])

    // ** Function in get data on page change
    const handlePagination = page => {
        getSummaries({
            sort,
            sortColumn,
            q: searchTerm,
            perPage: rowsPerPage,
            page: page.selected + 1,
            status: currentStatus.value,
            start: startDate,
            end: endDate
        })

        setCurrentPage(page.selected + 1)
    }

    // ** Function in get data on rows per page
    const handlePerPage = e => {
        const value = parseInt(e.currentTarget.value)
        getSummaries({
            sort,
            sortColumn,
            q: searchTerm,
            perPage: value,
            page: currentPage,
            status: currentStatus.value,
            start: startDate,
            end: endDate
        })
        setRowsPerPage(value)
    }

    // ** Function in get data on search query change
    const handleFilter = val => {
        setSearchTerm(val)
        getSummaries({
            sort,
            q: val,
            sortColumn,
            page: currentPage,
            perPage: rowsPerPage,
            status: currentStatus.value,
            start: startDate,
            end: endDate
        })
    }

    // ** Custom Pagination
    const CustomPagination = () => {
        const count = Number(Math.ceil(total / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    // ** Table data to render
    const dataToRender = () => {
        const filters = {
            status: currentStatus.value,
            q: searchTerm
        }

        const isFiltered = Object.keys(filters).some(function (k) {
            return filters[k].length > 0
        })

        if (data.length > 0) {
            return data
        } else if (data.length === 0 && isFiltered) {
            return []
        } else {
            return data.slice(0, rowsPerPage)
        }
    }

    const handleSort = (column, sortDirection) => {
        setSort(sortDirection)
        setSortColumn(column.sortField)
        getSummaries({
            sort,
            sortColumn,
            q: searchTerm,
            page: currentPage,
            perPage: rowsPerPage,
            status: currentStatus.value,
            start: startDate,
            end: endDate
        })

    }

    const exportList = () => {

        const params = {
            sort,
            sortColumn,
            export: true,
            q: searchTerm,
            page: currentPage,
            perPage: rowsPerPage,
            status: currentStatus.value,
            start: startDate,
            end: endDate
        }

        axios.post('/taskreport/performancereport', params)
            .then(async (res) => {

                const result = await res.data.result.results.map((obj) => {

                    const data = {}
                    data['name'] = obj.name
                    data['completed'] = obj.totaltasks
                    data['todo'] = obj.pendingtasks
                    data['inprogress'] = obj.completedtasks
                    data['senttoreview'] = obj.completedtasksontime

                    return data
                })

                const ws = await XLSX.utils.json_to_sheet(result, { origin: 'A2', skipHeader: true })
                const Heading = [['Name', 'Total Tasks', 'Total Pending Tasks', 'Total Completed Tasks', 'Total Completed on Time']]
                XLSX.utils.sheet_add_aoa(ws, Heading)
                const wb = { Sheets: { data: ws }, SheetNames: ["data"] }
                const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" })
                const data = new Blob([excelBuffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8" })
                FileSaver.saveAs(data, 'Task_Performance_Report.xlsx')

            })
            .catch((err) => {
                console.log(err)
            })

    }

    const applyFileter = async (val) => {
        getSummaries({
            sort,
            q: val,
            sortColumn,
            page: currentPage,
            perPage: rowsPerPage,
            start: val.startDate,
            end: val.endDate
        })

        setDataId(val.id)
        setStartDate(val.startDate)
        setEndDate(val.endDate)
    }

    return (
        <Fragment>
            <Card className='overflow-hidden'>
                <div className='react-dataTable'>
                    <DataTable
                        noHeader
                        subHeader
                        sortServer
                        pagination
                        responsive
                        paginationServer
                        columns={columns}
                        onSort={handleSort}
                        sortIcon={<ChevronDown />}
                        className='react-dataTable'
                        paginationComponent={CustomPagination}
                        data={dataToRender()}
                        subHeaderComponent={
                            <CustomHeader
                                searchTerm={searchTerm}
                                rowsPerPage={rowsPerPage}
                                handleFilter={handleFilter}
                                handlePerPage={handlePerPage}
                                exportList={exportList}
                                applyFileter={applyFileter}
                                dataId={dataId}
                            />
                        }
                    />
                </div>
            </Card>
        </Fragment>
    )
}

export default UsersList
